"use client";

import { Project } from "@/types/Project";
import ProjectCardModal from "./ProjectCardModal";

interface ProjectGridProps {
  projects: Project[];
  onClose: () => void;
}

export default function ProjectGrid({ projects, onClose }: ProjectGridProps) {
  return (
    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
      {projects.map((project) => (
        <ProjectCardModal key={project.id} project={project} onClose={onClose} />
      ))}
    </div>
  );
}
