"use client";

import { useRouter } from "next/navigation";
import { Project } from "@/types/Project";

interface ProjectCardModalProps {
  project: Project;
  onClose: () => void;
}

export default function ProjectCardModal({ project, onClose }: ProjectCardModalProps) {
  const router = useRouter();
  const { slug, title, description, status } = project;

  const handleDetailsClick = () => {
    onClose();
    router.push(`/projects/${slug}`);
  };

  const getStatusStyles = () => {
    switch (status) {
      case "completed":
        return "bg-green-500/20 text-green-400 border-green-500/30";
      case "in-progress":
        return "bg-yellow-500/20 text-yellow-400 border-yellow-500/30";
      case "planned":
        return "bg-blue-500/20 text-blue-400 border-blue-500/30";
      case "beta-test":
        return "bg-purple-500/20 text-purple-400 border-purple-500/30";
      default:
        return "bg-gray-500/20 text-gray-400 border-gray-500/30";
    }
  };

  const getStatusLabel = () => {
    switch (status) {
      case "beta-test":
        return "Beta Test";
      case "in-progress":
        return "In Progress";
      default:
        return status.charAt(0).toUpperCase() + status.slice(1);
    }
  };

  return (
    <div className="bg-gradient-to-br from-gray-900/50 to-black/50 rounded-xl p-6 border border-gray-700 hover:border-gray-500 transition-all duration-300 hover:shadow-lg hover:shadow-blue-500/10 flex flex-col min-h-[200px]">
      {/* Header with title and status */}
      <div className="flex items-start justify-between mb-3">
        <h3 className="text-xl font-semibold text-white leading-tight flex-1 pr-3">
          {title}
        </h3>
        <span
          className={`px-3 py-1 rounded-full text-xs font-medium border whitespace-nowrap ${getStatusStyles()}`}
        >
          {getStatusLabel()}
        </span>
      </div>

      {/* Description */}
      <p className="text-gray-400 text-sm leading-relaxed flex-1 mb-4 line-clamp-3">
        {description}
      </p>

      {/* Details Button */}
      <button
        onClick={handleDetailsClick}
        className="w-full py-3 bg-white/10 hover:bg-white/20 text-white font-medium rounded-lg transition-all duration-200 flex items-center justify-center gap-2 group"
      >
        Details
        <svg
          className="w-4 h-4 transform group-hover:translate-x-1 transition-transform"
          fill="none"
          stroke="currentColor"
          viewBox="0 0 24 24"
        >
          <path
            strokeLinecap="round"
            strokeLinejoin="round"
            strokeWidth={2}
            d="M9 5l7 7-7 7"
          />
        </svg>
      </button>
    </div>
  );
}
